/***** PAGE HEADER ******/

.contact__hdr-wrap {
  margin: 0 auto;
  text-align: center;
  width: 100%;
  z-index: 1000;
  box-sizing: border-box;
  font-size: calc(1em + 1vmin);
  background: 
    linear-gradient(45deg, rgba(19,19,19,.2) 0%, rgba(19,19,19,.1) 30%, rgba(19,19,19,.05) 100%),
    radial-gradient(rgba(255,255,255,.5) 0%, rgba(255,255,255,0) 100%),
    linear-gradient(to bottom, rgba(208,240,79,0) 0%, rgba(208,240,79,.1) 100%),
    rgba(245,245,245,.8);
  padding-top: 100px;
  padding-bottom: 100px;
  border-bottom: 3px solid #AECAC9;
  position: relative;
  z-index: 100;
  /*mask-image: linear-gradient(to bottom, rgba(0,0,0,0) 0px, rgba(0,0,0,1) 100%);*/
  /*mask-repeat: space;*/
}
.contact__hdr {
  font-size: calc(1.2em + 1vmin);
}

.contact__hdr-hdr {
  font-family: 'Roboto Condensed';
  font-weight: 700;
  font-style: italic;
  color: #000000;
  background: 
    linear-gradient(to bottom, #cc0000 0%, #cc0000 30%, #000 100%);
  background:
    -webkit-linear-gradient(to bottom, #cc0000 0%, #3f3f53 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.contact__hdr-txt {
  padding: 0 calc(10 * 1.5vw);
  margin: auto;
  color: #628a88;
}

/***** END PAGE HEADER ******/


.contact__email {
  color: #649795;
  text-decoration: none;
}
.contact__email:hover {
  text-decoration: underline;
}